"use strict";

var dispatcher = dispatcher;
var constants = constants;
var ctx = ctx;

var folderActions = {
	load: function load() {

		ctx.getFolderTree().then(function (data) {

			dispatcher.dispatch({
				action: constants.FOLDERS_LOADED,
				data: data
			});
		});
	},
	setSelected: function setSelected(id) {

		dispatcher.dispatch({
			action: constants.SELECTED_FOLDER_CHANGED,
			data: id
		});
	}
};